header{
    background: #005AA7; 
    background: -webkit-linear-gradient(to right, #FFFDE4, #005AA7);  
    background: -webkit-gradient(linear, left top, right top, from(#FFFDE4), to(#005AA7));  
    background: -webkit-linear-gradient(left, #FFFDE4, #005AA7);  
    background: -o-linear-gradient(left, #FFFDE4, #005AA7);  
    background: linear-gradient(to right, #FFFDE4, #005AA7); 
    padding-bottom: 20px;
}


